module.exports = {
  specialMoves: {},

  //RNBQKBNRPPPPPPPP----------------
  //----------------pppppppprnbqkbnr
  convertBoard: (board) => {
    let boardString = "";
    for (let i = 0, n = board.length; i < n; i++) {
      for (let j = 0, m = board[i].length; j < m; j++) {
        let piece = board[i][j][0];
        let type = piece.split('-')[1];
        if (type === 'l') {
          boardString += '-';
        } else if (piece.split('-')[0] === 'b') {
          boardString += type.toUpperCase();
        } else {
          boardString += type;
        }
      }
    }
    return boardString;
  },

  color: (piece, color) => {
    if (color === 'w') {
      return (/[a-z]/.test(piece));
    } else if (color === 'b') {
      return (/[A-Z]/.test(piece));
    }
    return false;
  },

  findAllPieces: (board, color) => {
    let positions = [];
    for (let i = 0, n = board.length; i < n; i++) {
      let piece = board[i];
      if (piece !== '-' && color(piece,color)) {
        positions.push(i);
      }
    }
    return positions;
  },

  findAllLegalMovesByPiece: () => {

  },

  canMove: () => {

  },


};



